/* Cart.css */

.cart-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.empty-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
  font-size: 24px;
  color: #555;
  text-align: center;
}


.empty-icon {
  font-size: 48px;
  margin-bottom: 10px;
  color: #ffaa00; /* orange color */
}
