/* QuantityCounter.css */

.quantity-counter-box {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  .quantity-counter-box button {
    width: 30px;
    height: 30px;
    background-color: #ffffff;
    border: 1px solid #cccccc;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    cursor: pointer;
  }
  
  .quantity-counter-input {
    width: 40px;
    height: 30px;
    border: 1px solid #cccccc;
    text-align: center;
    margin: 0 10px;
    padding: 0;
    font-size: 16px;
  }
  