.navbar {
  background-color: #333;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  color: #ffaa00;
  font-size: 24px;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.navbar-links {
  list-style-type: none;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
}

.navbar-links li {
  margin-left: 10px;
}

.navbar-button {
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px 12px;
  text-decoration: none;
  transition: opacity 0.3s ease;
  display: flex;
  align-items: center;
}

.navbar-button:hover {
  opacity: 0.8;
  color: #ffaa00;
}

.navbar-button:focus {
  outline: none;
}

.navbar-button:active {
  transform: translateY(1px);
}

.navbar-icon {
  font-size: 24px;
  margin-right: 5px;
}

.content {
  text-align: center;
  margin-top: 35vh;
}

h1 {
  font-size: 32px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

p {
  font-size: 18px;
  margin-bottom: 20px;
  color: #555;
}

.shop-now-button {
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  background-color: #ffaa00;
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  text-decoration: none;
  transition: opacity 0.3s ease;
}

.shop-now-button:hover {
  opacity: 0.8;
}

.featured-items {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.item {
  text-align: center;
  margin: 0 20px;
}

.item img {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.item h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 5px;
  color: #333;
}

.item p {
  font-size: 16px;
  color: #777;
}
