.productdetail-container {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    height: 80vh;
  }
  
  .productdetail-info {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .productdetail-image {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .productdetail-image img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background-color: orange;
  }
  
  .productdetail-heading {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .productdetail-price {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .productdetail-warranty {
    margin-bottom: 10px;
  }
  
  .productdetail-key-features {
    margin-bottom: 20px;
  }
  
  .productdetail-key-features h2 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .productdetail-key-features ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .productdetail-key-features li {
    margin-bottom: 5px;
  }
  
  .quantity-container {
    margin-bottom: 20px;
  }
  
  .back-button {
    background-color: orange;
    border: none;
    color: #fff;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  
  .back-button:hover {
    background-color: #ff8800;
  }
  
  .back-button:active {
    background-color: #e67600;
  }
  